import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import React from "react";
import Slider from "react-slick";
import ImageContainer from './ImageContainer';


export default function Slideshow(props){
  console.log("images: ", props.images)
  const numImages = props.images.length;

  const settings = {
    dots: false,
    adjustHeight: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 992,
        settings: {
          slidesToShow: Math.min(3, numImages),
          infinite: numImages > 3,
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: Math.min(2, numImages),
          infinite: numImages > 2,
        }
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          infinite: numImages > 1,
      }
      }
    ]
  };
    return (<Slider {...settings} className="detail-page-carousel">
            {props.images.map((image, index) => {
                var description = props.descriptions[index] ?? '';
                return <ImageContainer source={image} text={description} edit={false}/>
            })}
        </Slider>)
}

