import React from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import {useAuth} from '../context/Auth'

export default function Header(){

    const { authTokens } = useAuth();

    return ( 
        <Navbar expand="md" className="navbar" bg="success" text-variant="warning" style={{padding: '0 3rem'}}>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav>
                <LinkContainer to={"/home"} className="link">
                    <Nav.Link>Home</Nav.Link>
                </LinkContainer>
                <LinkContainer to={"/kataPengantar"} className="link">
                    <Nav.Link>Kata Pengantar</Nav.Link>
                </LinkContainer>
                <LinkContainer to={"/overview"} className="link">
                    <Nav.Link>Overview</Nav.Link>
                </LinkContainer>
                {authTokens 
                    ? (<LinkContainer to={'/edit/new'} className="link create-new">
                        <Nav.Link>Create new Gulma</Nav.Link>
                        </LinkContainer>)
                    : (<LinkContainer to={'/login'} className="link create-new">
                        <Nav.Link>Admin</Nav.Link>
                        </LinkContainer>)}
            </Nav> 
            </Navbar.Collapse>   
        </Navbar>
        
    )
}