import React from 'react';
import TableRow from './TableRow';
import {DisplayProps} from '../constants/DisplayProps'
import { Nav } from 'react-bootstrap';
import Slideshow from './Slideshow';
import {LinkContainer} from 'react-router-bootstrap';
import {useAuth} from '../context/Auth'


export default function Card(props){

    var gulma = props.gulma;
    const { authTokens } = useAuth();

    return (
        <div>
        <h4 className="detail-header">{gulma['LatinName']}</h4>
        
        <Slideshow images={gulma.Images} descriptions={gulma.ImageDescriptions}/>

        {Object.keys(gulma).map(prop => DisplayProps[prop] ? <TableRow name={DisplayProps[prop]} value={gulma[prop]}/> : null)}
        {authTokens && <LinkContainer to={'/edit/' + gulma.Url} className='link edit'>
                    <Nav.Link>Edit Gulma</Nav.Link>
                </LinkContainer>}
        </div>
    )
}

