import React, {useState, useEffect} from 'react';
import {useParams, Redirect} from "react-router-dom";
import { Button, Form } from 'react-bootstrap';
import EditTableRow from '../components/EditTableRow';
import AlertDialog from '../components/AlertDialog';
import {emptyGulma} from '../constants/EmptyGulma';
import fetch from 'node-fetch';
import axios from 'axios';
import ImageContainer from '../components/ImageContainer';
import {ApiUrl} from '../constants/ApiUrl';
import {token} from "../context/DropBox"


export default function EditGulma(){

    const editProps = ["NamaLokal","Sinonim","Biologi","Morfologi","Pemanfaatan","Pengendalian","Permasalahan","TinjaunanUmum"]

    const plantName = useParams()?.plantname;

    const [create, setCreate] = useState(plantName === null);
    const [gulma, setGulma] = useState();
    const [redirect, setRedirect] = useState(false);
    const [openDialog, setOpenDialog] = useState(false);

    useEffect(() => {
        if(create){
            setGulma(emptyGulma);
        } else {
            fetch(`${ApiUrl}/gulmanames`, {headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            }})
            .then(res=>res.json())
            .then(data => {
                if(data.includes(plantName)){
                    setCreate(false);
                    fetch( `${ApiUrl}/gulma/${plantName}`, {headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                    }})
                        .then(res => res.json())
                        .then(data => setGulma(data))
                        .catch(error => console.log(error));
                } else {
                    setCreate(true);
                    setGulma(emptyGulma);
                }
            })
        }
    }, [plantName, create])

    function changeHandler(event){
        var newGulma = JSON.parse(JSON.stringify(gulma));
        newGulma[event.target.id] = event.target.value;
        newGulma["Url"] = newGulma["LatinName"].replace(" ", "");
        setGulma(newGulma);
    }

    function changeImageDesc(event, index){
        var newGulma = JSON.parse(JSON.stringify(gulma));
        newGulma["ImageDescriptions"][index] = event.target.value;
        setGulma(newGulma)
    }

    function handleSubmit(method){
        fetch( `${ApiUrl}/gulma/${plantName}`, {
        method: method,
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(gulma)})
        .catch(error => console.log(error));
        setRedirect(true);
    }

    function handleConfirm(){
        setOpenDialog(false);
        handleSubmit("DELETE");
    }

    function handleAbort(){
        setOpenDialog(false);
    }

    function onImageUpload(){
        var file = document.getElementById('imageUpload').files[0];
        var dbxHeader = {
            'path' : `/${plantName}/${file.name}`
        }
        axios({
            url: 'https://content.dropboxapi.com/2/files/upload',
            method: 'POST',
            headers: {
                'Authorization' : 'Bearer ' + token,
                'Content-Type': 'application/octet-stream',
                'Dropbox-API-Arg': JSON.stringify(dbxHeader)
            },
            data: file
        })
        .then(()=> {
            axios({
                url: 'https://api.dropboxapi.com/2/sharing/create_shared_link_with_settings',
                method: 'POST',
                headers: {
                    'Authorization' : 'Bearer ' + token,
                    'Content-Type': 'application/json',
                },
                data: {
                    'path': `/${plantName}/${file.name}`
                }
            })
            .then(response => {
                var url = response.data.url.replace("dropbox", "dl.dropboxusercontent").replace("?dl=0", "");
                console.log(url)
                var newGulma = JSON.parse(JSON.stringify(gulma));
                newGulma["Images"].push(url);
                newGulma["ImageDescriptions"].push("");
                setGulma(newGulma)
            })
            .catch(error => console.log(error));
        })
        .catch(err=>console.log(err))
    }

function deleteImage(index){
    var newGulma = JSON.parse(JSON.stringify(gulma));
    newGulma['Images'] = gulma['Images'].filter((image, idx) => idx !== index);
    newGulma['ImageDescriptions'] = gulma['ImageDescriptions'].filter((image, idx) => idx !== index);
    console.log(newGulma)
    setGulma(newGulma)
}

if(redirect){
    return <Redirect to='/'/>
} else if(gulma){
    return (<div className="container">
        <h4 className="edit-header">{(create ? "Create new gulma" : `Edit ${gulma["LatinName"]}`)}</h4>
        <AlertDialog show={openDialog} onConfirm={handleConfirm} onAbort={handleAbort} name={gulma["LatinName"]}/>
        <Form >
            {create && <EditTableRow name="Nama Latin" value="" onchange={changeHandler}/>}
            {editProps.map(prop => <EditTableRow name={prop.match(/[A-Z][a-z]+/g).join(" ")} value={gulma[prop]} onchange={changeHandler}/>)}
            {gulma.Images.map((image, index) => {
                return (<div>
                    <ImageContainer source={image} edit={true} onDelete={()=>deleteImage(index)}/>
                     <EditTableRow name={`Image description ${index+1}`} value={gulma.ImageDescriptions[index] ?? ''} onChange={(event) => changeImageDesc(event, index)}/>
                </div>)
            })}
            <div> 
                <label for="imageUpload" className="right-col">Upload a new image:</label> <input type="file" id="imageUpload" className="btn btn-success" onChange={onImageUpload} accept="image/png, image/jpeg"/> 
            </div> 
            <div className="edit-buttons">
            <Button variant="danger" type="button" name="DELETE" onClick={()=>setOpenDialog(true)}>Delete entry</Button>
            <Button variant="success" type="submit" name="POST" onClick={()=>handleSubmit("POST")}>Submit</Button>
            </div>
        </Form>
        </div>)   
}
else {
    return (<div>Wait a minute</div>);
}   
}