import React, {useState} from 'react';
import { Image } from 'react-bootstrap';
import AlertDialog from './AlertDialog'


export default function ImageContainer(props){

    const [openImageDialog, setOpenImageDialog] = useState(false);

    return (
    <div className={props.edit ? "image-container edit" : "image-container"}>
        <AlertDialog show={openImageDialog} 
        onConfirm={()=> {
        if(props.onDelete){
            props.onDelete();
        }
        setOpenImageDialog(false);
        }} 
        onAbort={()=>setOpenImageDialog(false)} 
        name="this image"/>
        {props.edit && <a onClick={()=>setOpenImageDialog(true)}>&#10060;</a>}
        <Image src={props.source} className="card-image" />
        <div className="image-desc">
            <em>{props.text}</em>
        </div>
    </div>
)}