import React from 'react';

export default function KataPengantar(){
    return (<div className="pengantar">
            <h1>        
                Kata Pengantar
            </h1>
            <p>
                Gulma merupakan tumbuhan pengganggu yang keberadaannya tidak dapat dipisahkan dari pembudidayaan tanaman, termasuk kelapa sawit. Keberadaannya di lapangan perlu dimonitor secara berkala untuk menghindari pertumbuhan yang berlebihan sehingga mengganggu pertumbuhan kelapa sawit. Di perkebunan kelapa sawit, meskipun gulma pada umumnya bersifat merugikan, namun pada kondisi lapangan cukup banyak gulma yang bermanfaat, terutama untuk konservasi musuh alami serangga hama kelapa sawit. Oleh karena itu, pengendaliannya di lapangan perlu dilakukan secara bijak, efektif dan efisien.
            </p>
            <p>
                Pengendalian gulma yang efektif dan efisien akan dicapai apabila praktisi kelapa sawit, terutama petugas di lapangan, mampu mengidentifikasi secara tepat jenis-jenis gulma di lapangan dan menentukan teknik pengendalian yang sesuai dengan kondisi di lapangan. Buku “Gulma Kelapa Sawit di Indonesia: Jenis dan Pengendaliannya” menyajikan informasi mengenai jenis-jenis gulma yang umum dijumpai di perkebunan kelapa sawit di Indonesia dan penting untuk diketahui. Buku ini disusun secara sistematik, dilengkapi dengan foto-foto gulma sebagai panduan identifikasi dan disajikan dengan bahasa umum yang mudah dimengerti oleh seluruh kalangan. Informasi mengenai teknik pengendalian gulma terpadu di perkebunan kelapa sawit juga turut disertakan dalam buku ini.
            </p>
            <p>
                Kami berharap buku ini dapat bermanfaat bagi seluruh praktisi perkebunan kelapa sawit terutama dalam proses identifikasi dan pengendalian gulma. Kami mengucapkan terima kasih pada seluruh pihak yang telah berpartisipasi dalam proses penyusunan buku ini. Saran dan kritik yang membangun akan senantiasa kami terima untuk pengembangan dan penyempurnaan buku ini di masa mendatang. 
            </p>
            <p>
                Penulis
            </p>
    </div>) 
}