import React from 'react';
import { Form } from 'react-bootstrap';

export default function TableRow(props){

    var rows = ["Sinonim","Nama Lokal", "Nama Latin"].includes(props.name) || props.name.match("^Image") ? 1 : 5 

    return (
        <Form.Group className="table-row" controlId={props.name}>
            <Form.Label className="left-col">{props.name}</Form.Label>
            <Form.Control as="textarea" rows={rows} className="right-col" type="text" value={props.value} onChange={props.onchange}/>
        </Form.Group>) 
}