import React, {useState, useEffect} from 'react';
import { Spinner, Card, Col } from 'react-bootstrap';
import { LinkContainer } from "react-router-bootstrap";
import {LazyLoadImage} from "react-lazy-load-image-component";
import 'react-lazy-load-image-component/src/effects/blur.css';
import {ApiUrl} from '../constants/ApiUrl';


export default function HomeCard(props){

    var plantName = props.plantname;

    const [path, setPath] = useState();
    const [title, setTitle] = useState();
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        if(!mounted){
            fetch( `${ApiUrl}/gulma/${plantName}`, {headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      }})
        .then(res => res.json())
        .then(data => {
            setTitle(data['LatinName']);
            setPath(data.Images[0])
            //const b64 = new Buffer(image.data).toString('base64')
            //setPath(`data:${image.contentType};base64,${b64}`);
        })
        .catch(error => console.log(error));
        }
        return ()=>setMounted(true)
    }, [plantName, mounted])

    var imageContent = path 
    ? <LazyLoadImage effect="blur" className="home-card-image" src={path}/> 
    : <Spinner animation="border" role="status" style={{margin: 'auto'}}/>

    return(<Col xs={12} sm={6} md={4} lg={3}  >
            <LinkContainer to={'/'+plantName}>
            <Card className="container home-card link">
                <Card.Title className="home-card-title">{title}</Card.Title>
                <Card.Body style={{padding: 0}}>
                    {imageContent}
                </Card.Body>
            </Card>
            </LinkContainer> 
    </Col>)
}