// jshint esversion:6


import React, {useState} from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Header from "./components/Header";
import Home from "./views/Home";
import Login from "./components/Login";
import Detail from './views/Detail';
import EditGulma from './views/EditGulma';
import Overview from './views/Overview';
import KataPengantar from './views/KataPengantar';
import LandingPage from './views/LandingPage';
import Footer from './components/Footer';
import { AuthContext } from "./context/Auth";
import PrivateRoute from './context/PrivateRoute';

function App() {

  const existingTokens = JSON.parse(localStorage.getItem("tokens"));
  const [authTokens, setAuthTokens] = useState(existingTokens);
  
  function setTokens(data) {
    localStorage.setItem("tokens", JSON.stringify(data));
    setAuthTokens(data);
  }

  return (
    <div className="App">
    <AuthContext.Provider value={{ authTokens, setAuthTokens: setTokens }}>
    <Router basename="/bukugulma">
      <Header/>
      <Switch>
        <Route path='/' exact component={LandingPage}/>
        <Route path='/home' component={Home}/>
        <Route path='/login' component={Login}/>
        <Route path='/overview' component={Overview}/>
        <Route path='/kataPengantar' component={KataPengantar}/>
        <PrivateRoute path='/edit/:plantname' component={EditGulma}/>
        <Route path="/:plantname" component={Detail}>
        </Route>
      </Switch>
      <Footer/>
    </Router>
    </AuthContext.Provider>
    </div>
  );
}

export default App;
